.chart-key {
    display: flex;
    font-size: 0.8rem;
    justify-content: center;
    top: -5px;
    position: relative;
}

.key {
    display: flex;
    align-items: center;
}

.key-colour.line {
    width: 15px;
    height: 3px;
}

.key-colour.point {
    width: 7px;
    height: 7px;
    border-radius: 7px;
}

.key-colour.pb {
    background-color: #33a532;
}

.key-colour.sb {
    background-color: #ffbf00;
}

.key-colour.performance {
    background-color: black;
}
