.header {
    display: flex;
    justify-content: space-between;
    height: 40px;
    border-bottom: 1px solid black;
    z-index: 4;
}

.title {
    font-family: "Graduate";
    font-size: 2.2rem;
}

.menu {
    width: 7vh;
}

.search {
    width: 7vh;
}

.header-menu {
    position: relative;
    height: 100%;
    width: 100%;
    z-index: 3;
}

.menu-content {
    display: flex;
    position: absolute;
    border: 1px solid black;
    border-left: none;
    flex-direction: column;
    margin-top: -15%;
    width: 60vw;
    position: 0;
    z-index: 3;
}

.menu-content.hide {
    display: none;
}

.menu-item {
    line-height: 5vh;
    height: 5vh;
    background-color: #ffffff;
    text-align: center;
    cursor: pointer;
    color: black;
    text-decoration: none;
}

.menu-content a {
    color: black;
    text-decoration: none;
}

.menu-icon {
    height: 100%;
    width: 100%;
    padding-top: 15%;
}

.menu-icon .bar {
    width: 40%;
    height: 15%;
    border-bottom: 2px solid black;
    margin: auto;
}

.search-icon {
    width: 100%;
    height: 100%;
}

.search-icon .glass {
    width: 35%;
    height: 35%;
    border-radius: 100%;
    border: 2px solid black;
    margin: auto;
    margin-top: 25%;
    position: relative;
}

.search-icon .handle {
    position: relative;
    width: 20%;
    border-bottom: 2px solid black;
    transform: rotate(45deg);
    margin-left: 55%;
}

.app-menu-blanket {
    position: absolute;
    height: 100vh;
    width: 100vw;
    top: 40px; /* same as menu height */
    z-index: 2;
    background-color: rgba(255, 255, 255, 0.5);
    transition: background-color 250ms linear 0s;
    transition: display 0s linear 1s;
}

.app-menu-blanket.hidden {
    background-color: unset;
    display: none;
}
