.head-to-head-row {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0.5vh 0;
}

.values {
    display: flex;
    align-content: center;
    justify-content: space-between;
    width: 50%;
}

.head-to-head-row .bars {
    display: flex;
    align-items: center;
    width: 100%;
}

.head-to-head-row .bar-divider {
    height: 17px;
    width: 0.5%;
    background-color: #000000;
}

.head-to-head-row .bar {
    background-color: tomato;
    height: 100%;
}

.head-to-head-row .remainder {
    background-color: #cccccc;
    height: 80%;
}

.head-to-head-row .bar-container {
    display: flex;
    align-items: center;
    height: 15px;
    width: 50%;
}
