.athlete-search {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
}

.athlete-search > * {
    margin-top: 2vh;
}

.search-results {
    display: flex;
    flex-direction: column;
    width: 95vw;
}

.search-result-header {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
}

a {
    all: unset;
}

.search-result {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
    padding: 0.3vh 0;
    border-bottom: 1px solid black;
    cursor: pointer;
}

.search-result:hover {
    background-color: #cccccc;
}

.search-result .club {
    font-size: 0.9rem;
    text-align: right;
}

.sub-details {
    display: flex;
    font-size: 0.8rem;
    color: #888888;
}

.statistics-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.event-stats-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 100%;
}

.event-stats-card .name {
    font-size: 1.2rem;
    font-weight: 700;
}

.event-stats-card .personal-best {
    display: flex;
    align-items: center;
    font-size: 1.5rem;
}

.event-stats-card .season-best {
    display: flex;
    align-items: center;
    font-size: 1.2rem;
    color: #888888;
}

.event-stats-card .chart-title {
    font-weight: 700;
}

.event-stats-card .win-rate {
    width: 100%;
}

.event-stats-card .win-count {
    width: 45%;
}

.event-stats-card .label {
    font-size: 0.75rem;
    color: #888888;
}

.event-stats-card .best-label {
    position: relative;
    left: 100%;
    margin-left: 2px;
    font-size: 0.75rem;
    color: #888888;
    width: 0;
}

.event-stats-card .ranking {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

.event-stats-card .ranking .ranking-label {
    font-size: 0.8rem;
    line-height: 0.8rem;
}

.event-stats-card .ranking .ranking-year {
    font-size: 0.7rem;
    line-height: 1rem;
    color: #888888;
    margin-left: auto;
    margin-right: 2px;
}

.event-stats-card .ranking .ranking-labels {
    display: flex;
    align-items: center;
    width: 45%;
}

.event-stats-card .ranking .ranking-data {
    display: flex;
    align-items: center;
    width: 45%;
}

.event-stats-card .ranking .ranking-data .ranking-tick {
    display: flex;
    align-content: center;
    font-size: 0.8rem;
    line-height: 1rem;
}

.event-stats-card tr {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
    padding: 0.1vh 0;
    width: 100%;
    font-size: 0.8rem;
    line-height: 0.8rem;
}

.event-stats-card table {
    /* height: 15vh; */
    width: 90%;
}

.event-stats-card .chart-container {
    height: 25vh;
    width: 90vw;
}

.event-stats-card svg {
    height: 100%;
    width: 100%;
}

.athlete-details {
    margin: 1vh 5vw;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.athlete-details .name {
    font-weight: 600;
    font-size: 1.2rem;
    height: 100%;
    width: 50%;
}

.athlete-details .sub-details {
    justify-content: flex-end;
}

.athlete-details .sub-details .sex {
    margin-left: 2px;
}

.athlete-details .club {
    font-size: 0.85rem;
    justify-content: space-around;
}

.athlete-medals {
    display: flex;
    flex-direction: column;
    font-size: 0.8rem;
    margin: 0 5vw;
}

.athlete-medals .championship-medals {
    display: flex;
}

.athlete-medals .championship-medals .medal {
    margin-left: 2px;
}

.athlete-medals .gold-medal {
    color: #ffd700;
}

.athlete-medals .silver-medal {
    color: #808080;
}

.athlete-medals .bronze-medal {
    color: #cd7f32;
}

.season-selector {
    display: flex;
    width: 100%;
    justify-content: flex-start;
    font-size: 0.8rem;
    overflow-x: scroll;
    color: #808080;
}

.season-selector .season-year {
    margin: 0 1px;
    cursor: pointer;
}

.season-selector .selected {
    color: #000000;
}
