.head-to-head {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.athletes {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.athletes .athlete {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 45vw;
    /* height: 25vh; */
    /* border: 1px solid black; */
}

.comparison {
    width: 95vw;
}

.statistics {
    display: flex;
    flex-direction: column;
    align-items: stretch;
    width: 100%;
}

.results {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
}

.head-to-head .name {
    width: 100%;
    font-size: 1.2rem;
}

.head-to-head .sub-details {
    flex-direction: column;
}

.search-inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 7vh;
}

.search-inputs input {
    width: 90%;
}

.head-to-head .athlete-search-results {
    position: absolute;
    z-index: 2;
    top: 14vh;
    background-color: #ffffff;
    border: 1px solid black;
    width: 90vw;
}
