.search-page {
    display: flex;
    flex-direction: column;
    justify-content: stretch;
    align-items: center;
    width: 100%;
}

.search-page .result {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 1vw;
    padding: 0.3vh 0;
    border-bottom: 1px solid black;
    cursor: pointer;
    width: 100%;
}

.search-page .result .result-main-row {
    display: flex;
    justify-content: space-between;
}

.search-page .result .result-main-row .result-secondary {
    font-size: 0.9rem;
}

.search-page .result .result-sub-row .result-sub {
    font-size: 0.75rem;
    color: #888888;
}

.search-page .search-results {
    display: flex;
    flex-direction: column;
    width: 95vw;
}

.search-page .search-loading {
    text-align: center;
}

.search-results-header {
    display: flex;
    justify-content: space-between;
    margin: 0 1vw;
}
