.athlete-event-card {
    display: flex;
    width: 90%;
    height: 10vh;
    border: 1px solid #000000;
    margin: 1vh;
    align-items: center;
    flex-direction: column;
    border-radius: 3px;
    justify-content: space-between;
    transition: box-shadow 100ms linear 0ms;
}

.athlete-event-card.selected {
    border: 1px solid #3461eb;
    box-shadow: 0 0 5px #3461eb;
}

.athlete-event-card .main-row {
    display: flex;
    width: 95%;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.athlete-event-card .row-right {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 45%;
    min-width: 120px;
}

.athlete-event-card .ranking-details {
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    width: 50%;
}

.ranking-details .current-rank {
    text-align: center;
}

.ranking-details .rank-label {
    font-size: 0.75rem;
    color: #808080;
    line-height: 0.8;
    white-space: pre-wrap;
}

.ranking-details .ranking-tick {
    font-size: 0.775rem;
}

.athlete-event-card .bottom-row {
    position: relative;
    bottom: 0;
}

.athlete-event-card .name {
    font-weight: 600;
    font-size: 1.2rem;
}

.athlete-event-card .card-expand-collapse-button {
    cursor: pointer;
    font-size: 0.7rem;
    color: #808080;
    padding: 0 2px;
    margin-bottom: 1px;
}

.event-card-expanded {
    position: relative;
    top: -1vh;
    width: 88%;
    background-color: #eeeeee;
    z-index: -1;
}

.event-card-expanded.hidden {
    height: 0;
    color: rgba(0, 0, 0, 0);
}

.expanded-statistics {
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.9rem;
    margin-top: 5px;
}

.expanded-statistics .statistic-row {
    width: 80%;
    display: flex;
    justify-content: space-between;
}
