.performance-row {
    display: flex;
    width: 95%;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #000000;
    height: 5vh;
    border-radius: 3px;
    margin-bottom: 5px;
    cursor: default;
}

.performance-row.selected {
    border: 1px solid #3461eb;
    box-shadow: 0 0 5px #3461eb;
}

.performance-row .meeting {
    width: 50%;
    font-size: 0.7rem;
    color: #808080;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.performance-row .date {
    width: 17%;
    margin-right: 5px;
    font-size: 0.8rem;
    color: #808080;
    text-align: right;
}

.performance-row .performance {
    /* margin-left: 2px; */
    /* width: 20%; */
    text-align: right;
}

.performance-row .event {
    margin-left: 5px;
    /* width: 20%; */
    text-align: right;
}

.performance-row-expanded {
    position: relative;
    top: -1vh;
    width: 88%;
    background-color: #eeeeee;
    z-index: -1;
}

.performance-row-expanded.hidden {
    display: none;
}
