.meeting-page {
    display: flex;
    flex-direction: column;
}

.meeting-page .meeting-name {
    font-size: 1.5rem;
    margin: 0 2.5vw;
}

.meeting-page .meeting-details {
    display: flex;
    justify-content: space-between;
    margin: 0 2.5vw;
}

.meeting-page .meeting-details .meeting-location {
    font-size: 1.2rem;
}

.meeting-page .performances {
    display: flex;
    flex-direction: column;
    align-self: center;
    width: 95vw;
}

.meeting-page .performances .table-header {
    display: flex;
    justify-content: space-between;
    margin-top: 1vh;
    background-color: #cfcfcf;
    padding: 0 5px;
}

.meeting-page .performances .table-header .event {
    font-weight: 600;
}

.meeting-page .filter-panel {
    display: flex;
    justify-content: space-between;
    margin: 0 2.5vw;
}

.performances .gold-medal {
    color: #ffd700;
}

.performances .silver-medal {
    color: #808080;
}

.performances .bronze-medal {
    color: #cd7f32;
}
