.result-comparison-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 5px 0;
    width: 100%;
}

.result-comparison-row .details {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 70%;
}

.result-comparison-row .performance {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 15%;
}

.result-comparison-row .details .meeting {
    font-size: 0.8rem;
    color: #888888;
    overflow: hidden;
    text-align: center;
}

.result-comparison-row .performance .position {
    color: #888888;
    font-size: 0.8rem;
}
.result-comparison-row .performance .value {
    font-size: 1.1rem;
}
